/* src/components/AuthNav/AuthNav.module.css */

.homePage {
  position: absolute; /* Use absolute positioning */
  top: 132px; /* 50px from the top of the parent container */
  left: 765px; /* Align to the left of the parent */
  width: 575px; 
  height: 320px;
  /* Additional styles can go here */
}

.authNav {
  display: flex;
  margin-top: 26px;
}

.link {
  text-decoration: none;
  padding: 10px 20px;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 137px; 
  height: 47px;
  border-radius: 40px;
  margin: 0;
}

.link:hover {
  text-decoration: underline;
}

.header {
  padding: 20px;
  max-width: 1000px;
}

.header h4 {
  color: rgba(250, 250, 250, 0.4); 
  font-size: 15px;
  font-weight: bold;
}

.header h1 {
  color: #FAFAFA; /* Text color for h1 */
  font-size: 35px;
  font-weight: 700;
  margin: 10px 0;
}

.header h1 span {
  color: #0EF387; /* Text color for span */
  text-decoration: underline;
}

.header p {
  color: rgba(250, 250, 250, 0.4); /* Text color for paragraph */
  font-size: 15px;
  line-height: 1.5;
  margin-top: 20px;
}

.signUp {
  background-color: #0EF387; /* Green background */
  color: #0C0D0D; /* Dark text */
  margin-right: 12px; /* Space between "Sign Up" and "Sign In" */
}

.signIn {
  color: #FAFAFA; /* White text */
  border: 2px solid rgba(250, 250, 250, 0.4); /* Light border */
  margin-left: 0; /* No left margin */
}

.signUp:hover {
  background-color: #09c574; /* Darker green on hover */
}
